import {UniPopup} from 'components/uni/uni-popup/uni-popup'
import {ApplyYjSelect,ApplyCashwhere,GetRealnameauthentication} from 'api/user'
export default{
	components:{
		UniPopup
	},
	data(){
		return{
			data:{},
			load:false,
			show:false,
			realname:'',
			idcard:'',
			baseUrl:this.$store.state.SRC+'/config/app'
		}
	},
	methods:{
		close(e){
			this.show = e.show
		},
		open(url){
			ApplyCashwhere().then(res=>{
				if(res.result){
					uni.navigateTo({
						url:url
					})
				}else{
					this.show = true
				}
			})
			
		},
		save(){
			GetRealnameauthentication({
				idcard:this.idcard,
				realname:this.realname
			}).then(res=>{
				if(res.result){
					uni.navigateTo({
						url:'/pages/user/distribution/applycash'
					})
				}else{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
				}
			})
		}
	},
	onLoad() {
		ApplyYjSelect().then(res=>{
			if(res.result){
				this.data = res
				this.load = true
			}
		})
	}
}
